import React from "react";
import "./About1.css";

import foto from "../../images/bn.png";

const About1 = () => {
  return (
    <div className="A1Super" id="about1">
      <div className="A1Container">
        <div className="A1Left">
          <h1>What is Project Z.O.R.A.?</h1>
          <p>
            <span>Project Z.O.R.A.</span> is a collection of <span>1500</span>{" "}
            NFT's genetically engineered inside a laboratory in order to save
            the human race from the machines. Each Zora has a{" "}
            <span>unique</span> and distinct genetic trait, they are excellent
            at demonstrating their strength on and off the battlefield. Some can
            become <span>elite</span> warriors.
          </p>
        </div>
        <div className="A1Right">
          <img src={foto} />
        </div>
      </div>
    </div>
  );
};

export default About1;
