import React from "react";
import "./Footer.css";
import logoD from "../../images/discord.svg";
import logoT from "../../images/twitter.svg";
import logoZ from "../../images/lg2.png";

const Footer = () => {
  return (
    <div className="FooterSuper">
      <div className="FooterContainer">
        <div className="FooterLogo">
          <img src={logoZ} height="150" />
        </div>
        <div className="FooterSocials">
          <a href="https://twitter.com/ZORA_NFT" target="_blank">
            <img src={logoT} height="40" />
          </a>
          <a href="https://discord.gg/YpSHVYJC3W" target="_blank">
            <img src={logoD} height="40" />
          </a>
        </div>
        <div className="FooterCR">
          <p>2022 Tm ProjectZora Productions, All Rights Reserved</p>
        </div>
      </div>
    </div>
  );
};

export default Footer;
