import React, { useState } from "react";
import "./Navbar.css";
import { Link, animateScroll as scroll } from "react-scroll";
import { useNavigate } from "react-router-dom";

import logoZ from "../../images/lg2.png";
import logoD from "../../images/discord.svg";
import logoT from "../../images/twitter.svg";
import hamburger from "../../images/hamburger.png";
import close from "../../images/close-button.png";

const Navbar = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [menuImg, setMenuImg] = useState(hamburger);
  let navigate = useNavigate();

  function openMenu() {
    if (isMenuOpen) {
      setMenuImg(hamburger);
    } else {
      setMenuImg(close);
    }

    setIsMenuOpen(!isMenuOpen);
  }

  return (
    <div className="NavSuper">
      <div className="NavContainer">
        <div className="Logo">
          <Link
            to="hero"
            smooth={true}
            duration={700}
            spy={true}
            exact="true"
            offset={-74}
          >
            <img src={logoZ} />
          </Link>
        </div>
        <div className="Links">
          <Link
            to="about1"
            smooth={true}
            duration={700}
            spy={true}
            exact="true"
            offset={-74}
          >
            <p>THE PROJECT</p>
          </Link>
          <Link
            to="about2"
            smooth={true}
            duration={700}
            spy={true}
            exact="true"
            offset={-74}
          >
            <p>ORIGINS</p>
          </Link>
          <Link
            to="carousel"
            smooth={true}
            duration={700}
            spy={true}
            exact="true"
            offset={-74}
          >
            <p>THE ZORAS</p>
          </Link>
          <Link
            to="faq"
            smooth={true}
            duration={700}
            spy={true}
            exact="true"
            offset={-74}
          >
            <p>F.A.Q.</p>
          </Link>
        </div>
        <div
          className="UBtn"
          target="_blank"
          onClick={() => {
            navigate("/utilities");
          }}
        >
          <a>
            <span></span>
            <span></span>
            <span></span>
            <span></span>
            <p>UTILITIES</p>
          </a>
        </div>
        <div className="Wide Socials">
          <a href="https://twitter.com/ZORA_NFT" target="_blank">
            <img src={logoT} />
          </a>
          <a href="https://discord.gg/YpSHVYJC3W" target="_blank">
            <img src={logoD} />
          </a>
          <div className="MintBtn">
            <a>
              <span></span>
              <span></span>
              <span></span>
              <span></span>
              <p>MINT</p>
            </a>
          </div>
        </div>
        <div className="Movil">
          <button className="NavBtn" onClick={() => openMenu()}>
            <img src={menuImg} height="40" />
          </button>
        </div>
      </div>
      <div className={"NavPanel-" + (isMenuOpen ? "Open" : "Closed")}>
        <div className="PanelLinks">
          <Link
            to="about1"
            smooth={true}
            duration={700}
            spy={true}
            exact="true"
            offset={-74}
            onClick={() => openMenu()}
          >
            <p>THE PROJECT</p>
          </Link>
          <Link
            to="about2"
            smooth={true}
            duration={700}
            spy={true}
            exact="true"
            offset={-74}
            onClick={() => openMenu()}
          >
            <p>ORIGINS</p>
          </Link>
          <Link
            to="carousel"
            smooth={true}
            duration={700}
            spy={true}
            exact="true"
            offset={-74}
            onClick={() => openMenu()}
          >
            <p>THE ZORAS</p>
          </Link>
          <Link
            to="faq"
            smooth={true}
            duration={700}
            spy={true}
            exact="true"
            offset={-74}
            onClick={() => openMenu()}
          >
            <p>F.A.Q.</p>
          </Link>
        </div>
        <div className="PanelSocials">
          <a
            href="https://twitter.com/ZORA_NFT"
            target="_blank"
            onClick={() => openMenu()}
          >
            <img src={logoT} height="40" />
          </a>
          <a
            href="https://discord.gg/YpSHVYJC3W"
            target="_blank"
            onClick={() => openMenu()}
          >
            <img src={logoD} height="40" />
          </a>
        </div>
      </div>
    </div>
  );
};

export default Navbar;
