import React from "react";
import "./About2.css";

import foto from "../../images/WPN2.png";

const About2 = () => {
  return (
    <div className="A2Super" id="about2">
      <div className="A2Container">
        <div className="A2Left">
          <img src={foto} />
        </div>
        <div className="A2Right">
          <h1>Origin of Project Z.O.R.A.</h1>
          <p>
            It is the year <span>2112</span>.
            <br />
            <br />
            The human race is on the verge of <span>extinction</span>, our
            cities are being destroyed, the few remaining humans have formed{" "}
            <span>resistance</span> cells to fight against an intelligent
            entity, the <span>AI</span>, an artificial intelligence that
            controls the entire system and wants to <span>remake</span> it as an
            extension of it self. <br />
            <br />
            The technology and environment corporation{" "}
            <span>Zolarrazi Tech.</span> In order to save humanity, it has fused
            human dna with an unknown dna, in a moment of urgency, to help
            humans <span>fight</span> against the AI. <br />
            <br />
            Thus was born <span>Project Z.O.R.A.</span> (Zone Operation Rabbit
            Awakening)
          </p>
        </div>
      </div>
    </div>
  );
};

export default About2;
