import React, { Component, useState } from "react";
import ReactHowler from "react-howler";
import "./Music.css";

import bgmusic from "../../Audio/zora_master.mp3";
import playimg from "../../images/play.png";
import pauseimg from "../../images/pause.png";

const Music = () => {
  const [playing, setPlaying] = useState(false);
  const [firstPlay, setFirstPlay] = useState(true);

  const playPause = () => {
    setPlaying(!playing);
  };

  const firstLoad = () => {
    if (firstPlay) {
      setPlaying(true);
      setFirstPlay(false);
    }
  };

  return (
    <div>
      <ReactHowler
        playing={playing}
        src={[bgmusic]}
        volume="0.15"
        loop="true"
      />
      {firstLoad()}
      <div className="MusicBtn" onClick={playPause}>
        <img src={playing ? pauseimg : playimg} />
      </div>
    </div>
  );
};

export default Music;
